import { Box } from "components/Box/Box"
import { MyChart } from "components/Chart/Chart"
import { RangeV1 } from "components/Range/RangeV1"
import { RangeV2 } from "components/Range/RangeV2"
import { RangeV3 } from "components/Range/RangeV3"
import { RangeV4 } from "components/Range/RangeV4"
import { useMemo, useState } from "react"
import styled from "styled-components"

const Med = styled.div`
display: flex;
gap: ${p => p.theme.space[6]}px;
@media screen and (max-width: 700px) {
    flex-wrap: wrap;
}
`
const backblazePrice = {
  minPrice: 7,
  storagePrice: 0.005,
  transferPrice: 0.01,
};

const bunnyPrice = {
  maxPrice: 10,
  storagePriceHdd: 0.01,
  storagePriceSdd: 0.02,
  transferPrice: 0.01,
};

const scalewayPrice = {
  storagePriceMulti: 0.06,
  storagePriceSingle: 0.03,
  transferPrice: 0.02,
};

const vultrPrice = {
  minPrice: 5,
  storagePrice: 0.01,
  transferPrice: 0.01,
};

export const App = () => {
  const [valueR1, setValueR1] = useState(0);
  const [valueR2, setValueR2] = useState(0);
  const [hddSdd, sethddSdd] = useState(0);
  const [multiSingle, setMultiSingle] = useState(0);


  const rangeValue1 = (value) => {
    setValueR1(value[0]);
  };

  const rangeValue2 = (value) => {
    setValueR2(value[0]);
  };

  const rangeValue3 = (value) => {
    sethddSdd(value[0]);
  };

  const rangeValue4 = (value) => {
    setMultiSingle(value[0]);
  };

  const calculator = (price, range) => {
    return price * range
  };

  const fullBackblazePrice = useMemo(() => {
    return (calculator(backblazePrice.storagePrice, valueR1) + calculator(backblazePrice.transferPrice, valueR2)) < backblazePrice.minPrice
      ? backblazePrice.minPrice : calculator(backblazePrice.storagePrice, valueR1) + calculator(backblazePrice.transferPrice, valueR2);
  }, [valueR1, valueR2])

  const fullBunnyPrice = useMemo(() => {
    return ((hddSdd ? calculator(bunnyPrice.storagePriceSdd, valueR1) : calculator(bunnyPrice.storagePriceHdd, valueR1))
      + calculator(bunnyPrice.transferPrice, valueR2)) > 10
      ? 10 : ((!hddSdd ? calculator(bunnyPrice.storagePriceHdd, valueR1)
        : calculator(bunnyPrice.storagePriceSdd, valueR1)) + calculator(bunnyPrice.transferPrice, valueR2));
  }, [hddSdd, valueR1, valueR2])

  const fullScaleway = useMemo(() => {
    return (valueR1 <= 75 ? 0 : (multiSingle ? calculator(scalewayPrice.storagePriceMulti, (valueR1 - 75))
      : calculator(scalewayPrice.storagePriceSingle, (valueR1 - 75))))
      + (valueR2 <= 75 ? 0 : calculator(scalewayPrice.transferPrice, (valueR2 - 75)));
  }, [multiSingle, valueR1, valueR2])

  const fullVultrPrice = useMemo(() => {
    return (calculator(vultrPrice.storagePrice, valueR1) + calculator(vultrPrice.transferPrice, valueR2)) < vultrPrice.minPrice
      ? vultrPrice.minPrice : calculator(vultrPrice.storagePrice, valueR1) + calculator(vultrPrice.transferPrice, valueR2);
  }, [valueR1, valueR2])

  console.log('render');
  return (
    <Box display='flex' justifyContent='center' px='7' py='6'>
      <Box flex='0 1 800px'>
        <div>
          <MyChart backblazeCalc={fullBackblazePrice} bunnyCalc={fullBunnyPrice} scalewayCalc={fullScaleway} vultrCalc={fullVultrPrice} />
          <Box display='flex' justifyContent='center' flexWrap='wrap' gridGap='30px' mt='5' mb='7'>
            <RangeV3 value3={rangeValue3} />
            <RangeV4 value4={rangeValue4} />
          </Box>
        </div>
        <Med>
          <RangeV1 value1={rangeValue1} />
          <RangeV2 value2={rangeValue2} />
        </Med>
      </Box>
    </Box>
  );
};
